<template>
    <div class="home">
        <!-- 轮播图 -->
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item v-for="(item, index) in bannerData" :key="index">
                <img class="auto-img" v-lazy="item.bannerImg" alt="">
            </van-swipe-item>
        </van-swipe>

        <!-- 菜单栏 -->
        <div class="menu-content">
            <ul class="submenu">
                <li ref="type" v-for="(item, index) in typeData" :key="index" :class="{typeActive: num == index}" @click="onClick(item, index)">{{ item.typeDesc }}</li>
                <li class="line" :style="{top: top + 'px'}"></li>
            </ul>
            <ul class="menu-list">
                <li class="menu-item" v-for="(item, index) in goodsData" :key="index" @click="goParticulars(item.pid)">
                    <div class="item-img">
                        <img  class="auto-img" v-lazy="item.smallImg" alt="">
                    </div>

                    <div class="item-info">
                        <div class="name">
                            <p>{{ item.name }}</p>
                            <p class="enname">{{ item.enname }}</p>
                        </div>
                        <p class="pic">￥{{ item.price }}</p>
                    </div>
                </li>
            </ul>
        </div>

        <!-- 底部主导航组件 -->
        <NavBar></NavBar>
    </div>
</template>

<script>
    import { Toast } from 'vant';
    // 导入导航组件
    import NavBar from "@/components/NavBar";
    // 轮播图插件
    // import Swiper from "swiper";

    import { createNamespacedHelpers } from "vuex";
    const { mapState, mapMutations } = createNamespacedHelpers("homeModule");

    export default {
        name: "Home",

        data() {
            return {
                num: 0,
                top: 0,
                // 轮播图数据
                bannerData: [],
                //商品类型
                typeData: [],
                // 商品数据
                goodsData: [],
            };
        },
        methods: {

            ...mapMutations(["editData", "editGoods"]),

            onClick(item ,index) {
                this.num = index;

                // 获取高度
                let height = this.$refs.type[index].clientHeight;
                
                this.top = index * height;

                this.getGoodsData(item);
            },

            // 获取 banner 数据
            getBannerData() {

                // 获取缓存数据
                if(this.banner) {
                    this.bannerData = this.banner;
                    return;
                }

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/banner",
                    params: {
                        appkey: this.appkey,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();


                    if(res.data.code == 300) {
                        this.bannerData = res.data.result;
                        
                        // 缓存banner数据在vuex中
                        this.editData({ key: "banner", value: res.data.result });
                        
                    }
                    
                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });

            },

            // 获取商品类型
            getTypeData() {

                // 获取缓存数据
                if(this.type) {
                    this.typeData = this.type;
                    return;
                }

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/type",
                    params: {
                        appkey: this.appkey,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();


                    if(res.data.code == 400) {
                        // 添加最新推荐
                        res.data.result.unshift(
                            { type: "isHot", typeDesc: "最新推荐" }
                        );

                        this.typeData = res.data.result;

                        // 缓存banner数据在vuex中
                        this.editData({ key: "type", value: res.data.result });
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 根据商品类型获取数据
            getGoodsData(item) {

                if (this.goods[item.type]) {
                    this.goodsData = this.goods[item.type];
                    return;
                }

                this.goodsData = [];

                let params = {
                    appkey: this.appkey,
                    key: "type",
                    value: item.type,
                }

                // 最近推荐
                if(item.type == "isHot"){
                    params.key = "isHot";
                    params.value = 1;
                }

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/typeProducts",
                    params,
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();


                    if(res.data.code == 500){
                        this.goodsData = res.data.result;

                        // 缓存goods数据在vuex中
                        this.editGoods({ type: item.type, value: res.data.result });
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });

            },

            // 点击商品跳转详情
            goParticulars(pid) {
                // pid: 商品id
                this.$router.push({ name: "Particulars", query: { pid } });
            }

        },

        components: {
            NavBar,
        },

        computed: {
            ...mapState(["banner", "type", "goods"]),
        },

        created() {
            // 获取 banner 数据
            this.getBannerData();

            // 获取商品类型
            this.getTypeData();

            // 根据商品类型获取数据
            this.getGoodsData({ type: "isHot" });

            
            this.$store.state.active = 0;

        },

    }
</script>

<style lang="less" scoped>
    .home{
        display: flex;
        height: calc(100vh - 45px);
        color: #646566;
        flex-direction: column;
    }

    .van-swipe{
        height: 163px;
    }

    .menu-content {
        display: flex;
        flex: 1;
        overflow: hidden;

        .submenu{
            position: relative;
            width: 100px;
            height: 100%;
            background-color: #fff;
            overflow-y: auto;

            li {
                height: 40px;
                font-size: 14px;
                text-align: center;
                line-height: 40px;
                transition: .3s;

                &.typeActive {
                    color: #1989FA;
                    background-color: #f8f8f8;
                }
            }

            .line{
                position: absolute;
                top: 0;
                left: 0;
                width: 2px;
                background-color: #1989FA;
                transition: .3s;
            }
        }
        
        .menu-list {
            flex: 1;
            padding: 0 12px 12px;
            overflow-y: auto;

            .menu-item {
                display: flex;
                margin-top: 12px;
                padding: 12px;
                background-color: #fff;
                border-radius: 10px;

                .item-img{
                    width: 80px;
                }

                .item-info {
                    display: flex;
                    flex: 1;
                    margin-left: 10px;
                    flex-direction: column;
                    justify-content: space-between;

                    p{
                        font-size: 15px;
                        line-height: 1.2em;
                    }

                    .enname{
                        margin-top: 4px;
                        color: #D1C5C4;
                        font-size: 12px;
                    }

                    .pic{
                        color: #1989FA;
                    }
                }
            }
        }
    }
</style>